import { ShipRole, Team } from "../GameEngine";

export class ShipResources {
    public static GetSpriteBase64(team: Team, role: ShipRole): string {
        if (team === Team.TeamA) {
            switch(role) {
                case ShipRole.Mining: return this.spriteBlueMiningShipB64;
                case ShipRole.Hunting: return this.spriteBlueHuntingShipB64;
                case ShipRole.Blocking: return this.spriteBlueBlockingShipB64;
                default: return "";
            }
        }
        else {
            switch(role) {
                case ShipRole.Mining: return this.spriteYellowMiningShipB64;
                case ShipRole.Hunting: return this.spriteYellowHuntingShipB64;
                case ShipRole.Blocking: return this.spriteYellowBlockingShipB64;
                default: return "";
            }
        }
    }

    public static GetSpriteRotation() {
        return 90;
    }

    private static spriteYellowBlockingShipB64: string = `data:image/png;base64,`;

    private static spriteBlueBlockingShipB64: string = `data:image/png;base64,`;

    private static spriteYellowMiningShipB64: string = `data:image/png;base64,`;

    private static spriteBlueMiningShipB64: string = `data:image/png;base64,`;

    private static spriteYellowHuntingShipB64: string = `data:image/png;base64,`;

    private static spriteBlueHuntingShipB64: string = `data:image/png;base64,`;

    private static spriteShipB64: string = `data:image/png;base64,`;
}
